<template>
  <div>
    <a-card title="系统功能管理">
      <a-row :gutter="16">
        <a-col :span="24" style="max-width: 200px; margin-bottom: 12px;">
          <a-input v-model="searchForm.keyWord" placeholder="关键字查询..." allowClear @pressEnter="search" />
        </a-col>
        <a-col :span="24" style="width: 100px; margin-bottom: 12px;">
          <a-button type="primary" icon="search" @click="search">查询</a-button>
        </a-col>

        <a-col :span="24" style="display: flex; width: 360px; margin-bottom: 12px;">
          <a-button style="margin-right: 20px;" type="primary" icon="search" @click="updateOzonOrder">一键更新ozon订单数据</a-button>
          <div style="display: flex;">
            <a-input style="width: 120px;" v-model="upDateDays" placeholder="更新天数..." allowClear/>
          </div>
        </a-col>

        <a-col :span="24" style="width: 100px; margin-bottom: 12px; margin-right: 120px;">
          <a-button type="primary" icon="search" @click="updateOzonProduct">一键更新ozon商品数据</a-button>
        </a-col>

        <a-col :span="24" style="display: flex; width: 500px; margin-bottom: 12px; margin-right: 60px;">
          <a-upload
            :before-upload="beforeUpload"
            :file-list="fileList"
            :show-upload-list="false"
            :multiple="false"
            accept=".xlsx,.xls"
            style="margin-right: 10px;"
          >
            <a-button type="primary" icon="upload">上传 快递重量标准 Excel</a-button>
          </a-upload>

          <div v-if="fileName" style="margin: 10px 0; width: 140px; overflow: hidden;">
            {{ fileName }}
          </div>

          <a-button
            type="primary"
            :disabled="!selectedFile"
            @click="handleUpload"
          >
            确定上传
          </a-button>
        </a-col>

      </a-row>

      <a-row style="margin-top: 12px;">
        <a-table size="small" :columns="columns" :dataSource="items" rowKey="id" :loading="loading" :pagination="pagination" @change="tableChange"></a-table>
      </a-row>
    </a-card>
    <form-modal v-model="visible" :visible="visible" :form="targetItem" :type="type" @update="update" />
  </div>
</template>

<script>
import { getLogData,OzonOrderUpdate,OzonProductUpdate,updateOzonOrdersWeightStandardWithExcel } from '@/api/system'
import { message } from 'ant-design-vue';

export default {
  name: 'SystemProduct',
  components: {
    FormModal: () => import('./FormModal.vue'),
  },
  data() {
    return {
      columns: [
        {
          title: '日志ID',
          dataIndex: 'id',
        },
        {
          title: '操作对象',
          dataIndex: 'operate_type',
        },
        {
          title: '创建时间',
          dataIndex: 'create_time',
        },
        {
          title: '内容描述',
          dataIndex: 'describe',
        },
      ],
      searchForm: { start: 0, limit: 15, keyWord: ""},
      pagination: { current: 1, total: 0, pageSize: 15 },
      loading: false,
      items: [],

      visible: false,
      targetItem: {},
      form: {},
      type:"",
      upDateDays: 10,
      selectedFile: null, // 存储选中的文件对象
      fileName: '',       // 存储文件名
      fileList: [],       // 文件列表，用于控制上传行为
    };
  },
  computed: {
  },
  methods: {
    initialize() {
      this.list();
    },
    list() {
      this.loading = true;
      getLogData(this.searchForm).then(data => {
        console.log(data.data.data)
        this.pagination.total = data.data.count;
        this.items = data.data.data;
      }).finally(() => {
        this.loading = false;
      });
    },
    update() {
      this.visible = false
      this.list();
    },
    search() {
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    add(){
      this.type = "add",
      this.targetItem = {};
      this.visible = true;
    },
    openFormModal(item) {
      this.type = "modify",
      this.targetItem = { ...item };
      this.visible = true;
    },
    destroy(id) {
      deleteTeamData({ "id":id }).then(
        res => {
          this.$message.success(res.msg);
          this.list();
      });
    },
    tableChange(pagination, filters, sorter) {      
      this.searchForm.start = (pagination.current-1)*this.pagination.pageSize;
      this.pagination.current = pagination.current;
      this.list();
    },
    updateOzonOrder(){
      OzonOrderUpdate({"upDateDays": this.upDateDays}).then(
        res => {
          message.success(res.msg)
        }
      )
    },
    updateOzonProduct(){
      OzonProductUpdate().then(
        res => {
          message.success(res.msg)
        }
      )
    },
    // excel文件上传
    beforeUpload(file) {
      // 检查文件类型
      const isExcel =
        file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
        file.type === 'application/vnd.ms-excel';
      if (!isExcel) {
        this.$message.error('只能上传 Excel 文件');
        return false;
      }

      // 只允许选择一个文件，清空之前的
      this.selectedFile = file;
      this.fileName = file.name;
      this.fileList = [file]; // 更新文件列表，仅保留当前文件

      return false; // 阻止自动上传
    },

    // 手动触发上传
    async handleUpload() {
      if (!this.selectedFile) {
        this.$message.error('请选择文件后再上传');
        return;
      }

      updateOzonOrdersWeightStandardWithExcel(this.selectedFile).then(
        res => {
          message.success(res.msg)
          this.selectedFile = null;
          this.fileName = '';
          this.fileList = [];
          this.list()
        }
      )
    },
  },
  mounted() {
    this.initialize();
  },
  activated() {
    this.initialize();
  },
}
</script>