import request from '@/utils/request';

// ------------------------------店铺crud--------------------------------
// 获取店铺数据
export function getShopData(params) {
  return request({ url: `/shop/getData`, method: 'get', params })
}

// 修改店铺数据
export function modifyShopData(data) {
  return request({ url: `/shop/modifyData`, method: 'post', data })
}

// 新增店铺数据
export function addShopData(data) {
  return request({ url: `/shop/addData`, method: 'post', data })
}

// 删除店铺数据
export function deleteShopData(data) {
  return request({ url: `/shop/deleteData`, method: 'post', data })
}

