// 更新系统内ozon订单称重标准 updateOzonOrdersWeightStandardWithExcel
export function updateOzonOrdersWeightStandardWithExcel(file) {
  const formData = new FormData();
  formData.append('file', file);

  return request({
    url: '/general/updateOzonOrdersWeightStandardWithExcel',
    method: 'post',
    headers: {
      'Content-Type': 'multipart/form-data',
    },
    data: formData,
  });
}